/*Scroll bar styling*/
/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffffa6;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgb(0 0 0) 0%, rgb(0 0 0 / 76%) 35%, rgb(0 0 0) 100%);
  border-radius: 100px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #06066d;
}
