.App {
  font-family: sans-serif;
}

.clock {
  background: #ffffff42;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0 2px 30px rgba(0, 0, 0, 0.2);
  font-size: 24px;
  color: #444;
  text-align: center;
  border: 6px solid black;
}

.clock::after {
  background: #aaa;
  content: "";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 2px solid #fff;
}

.hour_hand {
  position: absolute;
  width: 6px;
  height: 36px;
  background: #000000;
  top: 29%;
  left: 47.5%;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  border-top-left-radius:100vw;
  border-top-right-radius:100vw;
}

.min_hand {
  position: absolute;
  width: 4px;
  height: 60px;
  background: #000000;
  top: 13.5%;
  left: 49%;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
}

.sec_hand {
  position: absolute;
  width: 2px;
  height: 60px;
  background: red;
  top: 16.5%;
  left: 49.5%;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
}

.clock span {
  position: absolute;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700;
}

